import { Header } from "@/components/header";
import { TemplateGrid } from "@/components/templates/template-grid";
import { getTemplates, getTemplatesDb, getTemplatesDbNew } from "@/lib/data";
import { UploadTemplateButton } from "./upload-template-button";

interface TemplateGridPageProps {
    db: 'main' | 'db' | 'db-new';
}

export async function TemplateGridPage({ db }: TemplateGridPageProps) {
  const dataFetchers = {
    'main': getTemplates,
    'db': getTemplatesDb,
    'db-new': getTemplatesDbNew,
  };

  const templates = await dataFetchers[db]();

  const allCategories = [
    ...new Set(templates.flatMap((template) => template.category)),
  ];

  const allFormats = [
    ...new Set(templates.map((template) => template.type || "Uncategorized")),
  ];

  return (
    <div className="flex min-h-screen w-full flex-col">
      <Header />
      <main className="flex flex-1 flex-col gap-4 p-4 md:gap-8 md:p-8">
        <div className="flex justify-end">
            <UploadTemplateButton db={db} />
        </div>
        <TemplateGrid templates={templates} categories={allCategories} formats={allFormats} db={db} />
      </main>
    </div>
  );
}
